@import url(https://fonts.googleapis.com/css?family=Orbitron:400,500,700,900|Roboto+Mono:300,400,500,700&display=swap);
body {
  background: #006064;
}

.TimerLengthSettings {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.TimerLengthSettings-wrapper {
  display: flex;
}

.TimerLengthSettings-title {
  font-size: 1.4em;
  margin-bottom: 0.2em;
}

.TimerLengthSettings-display {
  margin-right: 0.2em;
  margin-left: 0.2em;
  font-size: 1.3em;
  align-self: center;
}

.TimerLengthSettings button {
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}

.TimerLengthSettings-arrow {
  color: white;
  font-size: 2em;
}

.Timer {
  text-align: center;
  width: 230px;
  border: 4px solid #0097a7;
  padding: 1.3em 2em;
  margin-top: 2em;
  margin-bottom: 1em;
  border-radius: 2em;
  background: #00838f;
  display: block;
}
.Timer-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Timer-label {
  font-size: 2em;
  padding-bottom: 1em;
  font-weight: 900;
}

.Timer-display {
  font-size: 4em;
  font-family: "Orbitron", sans-serif;
  letter-spacing: 3px;
  font-weight: 600;
}

.TimerControl {
}

.TimerControl button {
  border: none;
  outline: none;
  background: transparent;
  color: white;
  font-size: 1.3em;
  cursor: pointer;
}

.App {
  background: #006064;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 80vh;
  color: white;
  font-family: "Roboto Mono", monospace;
}

.App .title {
  letter-spacing: 4px;
  text-transform: uppercase;
  font-size: 3em;
  font-weight: 900;
  font-family: "Orbitron", sans-serif;
  letter-spacing: 3px;
}

.App .wrapper {
  display: flex;
  justify-content: space-evenly;
  width: 50%;
}

